.header {
  gap: 3rem;
  margin: 0rem auto 3rem auto;
  width: 90%;
  max-width: 75rem;
  color: #312e28;
  font-size: 1rem;
  text-align: center;
}

.header h1 {
  font-family: 'Montserrat', sans-serif;
}

.highlight {
  background: linear-gradient(90deg, #302826, #777573);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main {
  width: 90%;
  max-width: 40rem;
  margin: 0 auto;
  text-align: center;
}

.main h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  margin-bottom: 3rem;
  color: #000000;
}

